import React from "react";

const Button = ({ label, onClick }) => {
  return (
    <button
      className="w-16 h-16 bg-blue-500 text-white rounded-md shadow-md hover:bg-blue-600 focus:outline-none focus:ring-2 focus:ring-blue-400"
      onClick={() => onClick(label)}
    >
      {label}
    </button>
  );
};

export default Button;
